html {
  box-sizing: border-box;
  background-color: #1b1b1b;
  height: -webkit-fill-available;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

/*
* {
  box-sizing: border-box;
  background-color: #1b1b1b;
}
*/

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
    margin: 0;
    height: -webkit-fill-available;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#navigation {
  background-color: #333;
  color: whitesmoke;

  display: flex;
  justify-content: space-between;
  
  align-items: center;
  text-align: center;
  
  gap: 2rem;
  padding: 0 15rem;
  height: 70px;
  top: 0px;

  position: -webkit-sticky;
  position: sticky;

  z-index: 1;
}

#navigation a {
  color: inherit;
  padding: 0 1rem;
}

#navigation ul {
  display: flex;
  flex-flow: row;
  list-style: none;
  
  text-align: inherit;
  position: absolute;
  
  margin: 50px 300px 0px 0px ;
  padding: 0;
  gap: 5rem;
}

#navigation li {
  background-color: #1b1b1b;
  padding: 0.2rem;
  font-weight: 500;
}

#nav-title {
  color: whitesmoke;
  font-size: 1.5rem;
  font-weight: 785;

  text-align: left;
  margin: 10px -10px 55px -20px;
  margin-top: 1rem;

  position: absolute;
}

#content {
  background-color: #1b1b1b;
  background-image: url('./media/bg.png');
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: contain;

  color: whitesmoke;
  align-items: center;
  text-align: center;
  display: flex;
  flex-flow: column;
  height: 90vh;
  
  z-index: 2;
}

#home {
  flex: 1;
  height: 100%;
}

.intro {
  font-size: 0.85rem;
  font-weight: 650;
  letter-spacing: 5px;
  color: goldenrod;
  background-color: #333;

  text-align: center;
  margin: 0 50px 0;
  padding: 0;

  height: 11.5px;
  position: static;
}

p.panel {
  border: 1px dotted rgb(105, 105, 105);
  background-color: rgba(17, 17, 17, 0.384);
  
  margin: 75px 50px 0px;
  padding: 15px;
  
  width: 600px;
  
  font-size: 1.25rem ;
  position: relative;
}

#about > .scroll-container {
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
  /*scroll-padding-top: 30px;*/
}

#about > .scroll-container > .scroll-section {
  scroll-snap-align: start;
}

#education {
  margin-top: 50px;
}
#education > .panel {
  margin-top: 0;
  margin-bottom: 15px;
  padding: 2px;
}

#experience {
  margin-top: 50px;
  margin-bottom: 50px;
}
#experience > .panel {
  margin-top: 0px;
}

#skills-list {
  display: inline-block;

  position: fixed;
  top: 185px;
  right: 340px;

  width: 300;
}

#skills-list h3 {
  text-align: left;
  margin: 0px 0px;
}

#skills-list li {
  text-align: left;
  list-style-position: inside;
  list-style-type:square;
  border: whitesmoke solid 1px;
  margin: 5px 45px 10px 0px;
  width: 200px;
}
#art > .panel > a:hover {
  color: goldenrod;
  font-size: 22px;
}
#art > .panel > a {
  color: whitesmoke;
  transition: ease-in-out;
  transition-duration: 0.85s;
  font-size: 18px;
}

#artwork-panel {
  background-color: #33333371;
  width: 55%;

  left: 25%;

  position: absolute;
  align-items: center;
  align-content: center;
  align-self: center;
  
  margin: 100px auto 50px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 30px;
}

.thumbnail {
  border: 3.5px solid whitesmoke;
  
  padding: 5px;
  margin: 5px 0px 0px 0px;

  width: 200px;
  height: 235px;

  transition: ease-in-out;
  transition-duration: 1s;
}

.thumbnail:hover {
  border: 3.5px solid goldenrod;

  padding: 10px;
  width: 250px;
  height: 230px;

  transition-duration: 1s;
  z-index: 2;

  position: relative;
}

.thumbnail > img {
  border: 3.5px solid goldenrod;
  width: 20%;
  filter: grayscale();
  transition: ease-in-out;
  transition-duration: .75s;
}
.thumbnail > img:hover {
  border: 3.5px solid goldenrod;
  width: 100px;
  filter: none;
  transition: ease-in-out;
  transition-duration: .75s;
}

#error-page {
  background-color: whitesmoke;
  color: #333;
  align-self: center;
  align-content: center;
  text-align: center;
}

.nav-link {
  color: inherit;
}
